@import url(https://fonts.googleapis.com/css2?family=Mukta&display=swap);
*
{
  margin: 0;
  padding: 0;
  font-family: 'Mukta', sans-serif;
}

.main_div
{
  width: 100%;
  height: 100vh;
  background: #ae7fc0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.center_div
{
  width: 25%;
  height: 80vh;
  background-color: #f4f4f4;
  box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);
  border-radius: 15px;
}

h1
{
  color: white;
  background: transparent;
  background-color: #8566aa;
  padding: 6px 0 2px 0;
  margin-bottom: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.5);
}

input
{
  text-align: center;
  height: 30px;
  top: 10px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width: 60%;
  border-bottom: 2px solid #8566aa;
  outline: none;
}

button
{
  min-height: 40px;
  width: 60px;
  border-radius: 50%;
  border-color: transparent;
  background-color: #8566aa;
  color: #fff;
  font-size: 40px;
  border: none;
  outline: none;
  margin-left: 10px;
  cursor: pointer;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.5);
}

button:hover
{
  background-color: #ae7fc0;
}

ol
{
  margin-top: 30px;
}

ol li
{
  padding-left: 0px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color:#8566aa;
  text-transform: capitalize;

}

.todo_style
{
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}

.todo_style .fa-times
{
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #eb2222;
  border-radius: 50%;
  margin: 0 15px 0 35px;
  color: aliceblue;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.5);
}

.todo_style .fa-times:hover
{
  color: rgb(100, 220, 20);
  background-color: #0a3d03;
}

.todo_style .fa-times:hover .list_style
{
  color: rgb(100, 220, 20);
  background-color: #0a3d03;
}

@media(max-width: 768px)
{
  .center_div
  {
    width: 55%;
  }

  .todo_style .fa-times
  {
     margin: 0 15px 0 15px;
     display: flex;
     justify-content: center;
     align-items: center;
  }

  button 
  {
    margin-left: 5px;
    min-height: 40px;
    width: 40px;
    color: #fff;
    font-size: 30px;
  }
}
